/*eslint-disable*/
'use strict';

import { MeshBuilder } from '@babylonjs/core/Meshes/meshBuilder'
import { StandardMaterial } from '@babylonjs/core/Materials/standardMaterial'
import { SolidParticleSystem } from '@babylonjs/core/Particles/solidParticleSystem'
import { QuadTree, Box, Point, Circle } from 'js-quadtree';
import { pauseProcessing } from '../utils.js'
import { Color4 } from '@babylonjs/core/Maths/math.color';

//Track series of events that happen to a particle over time and when
class VoxelEvent {
    constructor(filePosition, add) {
        this.filePosition = filePosition;  //Position where operation happens
        this.add = add; //Additve or Subractive Operation
    }
}

export default class VoxelRenderer {

    static drawDelay = 5;
    constructor(scene, voxelWidth, voxelHeight, specularColor, loadingProgressCallback, renderFuncs, tools) {

        //Core items from GCodeProcessor
        this.scene = scene;
        this.voxelWidth = parseFloat(voxelWidth);
        this.voxelHeight = parseFloat(voxelHeight);
        this.specularColor = specularColor;
        this.loadingProgressCallback = loadingProgressCallback;
        this.renderFuncs = renderFuncs;
        this.tools = tools;

        this.solidMat;
        this.transparentMat;

        //used for rendering visuals
        this.previousFilePosition = 0;
        this.currentFilePosition = 0;
        this.isLoading = false;
        this.scrubDistance = 2000;
    }

    //Used for rendering visual
    updateFilePosition(position) {
        this.previousFilePosition = this.currentFilePosition;
        this.currentFilePosition = position;
    }

    buildBox() {

        if (this.solidMat) {
            try {
                this.solidMat.dispose()
                this.solidMat = null;
            }
            catch {
                ;
            }
        }

        if (this.transparentMat) {
            try {
                this.transparentMat.dispose()
                this.transparentMat = null;
            }
            catch {
                ;
            }
        }

        //build the box
        let box = MeshBuilder.CreateBox('box', { width: this.voxelWidth, height: this.voxelHeight, depth: this.voxelWidth }, this.scene);
        //Build out solid and transparent material.
        this.solidMat = new StandardMaterial('solidMaterial', this.scene);
        this.solidMat.specularColor = this.specularColor;
        this.transparentMat = new StandardMaterial('transparentMaterial', this.scene);
        this.transparentMat.specularColor = this.specularColor;
        this.transparentMat.alpha = 0;
        this.transparentMat.needAlphaTesting = () => true;
        this.transparentMat.separateCullingPass = true;
        this.transparentMat.backFaceCulling = true;

        return box;
    }

    async render(lines) {
        this.isLoading = true;

        let that = this;
        let meshIndex = 0;
        let box = this.buildBox();
        //X, Y, Z
        let x = parseInt(300 / this.voxelWidth) + 1 //Bed Size
        let z = x; //Default to a square computed bed for now    
        let y = parseInt(300 / this.voxelHeight) + 1  //Max Height


        let layers = new Array(z);
        for (let ystep = 0; ystep <= y; ystep++) {
            layers[ystep] = new Object();
        }

        let maxRenderedLayer = 0
        //        let layerMap = {}  //Layer Map  two key dictionary for quick lookup ithout an array
        let layerHeight = 0;
        let lastUpdate = new Date();
        console.log("Start Point");
        /********************************************************************************************** */
        /*                                  Build the voxel model                                       */
        /********************************************************************************************** */
        for (var lineIdx = 0; lineIdx < lines.length; lineIdx++) {
            let gcodeLine = lines[lineIdx];
            let tool = this.tools[gcodeLine.tool];

            if (!gcodeLine.extruding) { continue; } //We only care about gcode lines which are performing an action such as extrusion or cutting

            let voxelArray = gcodeLine.getVoxelSegments(this.voxelWidth, this.voxelHeight, tool.getDiameter() / 2, tool.isAdditive());

            if (voxelArray === undefined || voxelArray.length === 0) continue;

            let voxelEvent = new VoxelEvent(gcodeLine.gcodeLineNumber, tool.isAdditive());
            for (let voxelIdx = 0; voxelIdx < voxelArray.length; voxelIdx++) {
                let voxel = voxelArray[voxelIdx];
                let idxX = Math.floor(voxel.x / this.voxelWidth);
                let idxY = Math.floor(voxel.y / this.voxelHeight);
                let idxZ = Math.floor(voxel.z / this.voxelWidth);

                if (idxY < 0) {
                    idxY = 0;
                }

                if (idxY != maxRenderedLayer && tool.isAdditive()) {
                    maxRenderedLayer = idxY;
                }

                if (tool.toolType === "Extruder") {

                    try {
                        var point = layers[idxY][idxX][idxZ];
                    }
                    catch (ex) {
                        point = null;
                    }

                    if (!point) {
                        try {
                            if (!layers[idxY].hasOwnProperty(idxX)) {
                                layers[idxY][idxX] = {};
                            }
                            layers[idxY][idxX][idxZ] = { color: gcodeLine.color, voxelEvents: [voxelEvent] };
                        }
                        catch (ex) {
                            console.log(ex);
                            console.log(`${idxX}  ${idxY} ${idxZ}`)
                        }
                    }
                    else {
                        //Find the point
                        //if (points.length > 0 && points[0].data && !points[0].data.voxelEvents.some(ev => ev.filePosition === voxelEvent.filePosition)) {
                            point.voxelEvents.push(voxelEvent);
                        //}
                    }
                }
                else { //Deal with cutting operation
                    for (let cutY = idxY; cutY <= maxRenderedLayer + 1; cutY++) {
                        try {
                            if (layers[cutY][idxX][idxZ]) {
                                layers[cutY][idxX][idxZ].voxelEvents.push(voxelEvent);
                            } else {
                                if (!layers[cutY].hasOwnProperty(idxX)) {
                                    layers[cutY][idxX] = {};
                                }
                                layers[cutY][idxX][idxZ] = { color: gcodeLine.color, voxelEvents: [voxelEvent] }
                            }
                        }
                        catch {
                            if (cutY < 0) continue;
                            if (!layers[cutY].hasOwnProperty(idxX)) {
                                layers[cutY][idxX] = {};
                            }
                            layers[cutY][idxX][idxZ] = { color: gcodeLine.color, voxelEvents: [voxelEvent] }
                        }

                    }
                }
            }
            lines[lineIdx] = null;

            if (lineIdx % 10000 === 0 || (new Date() - lastUpdate > 5000)) {
                lastUpdate = new Date();
                this.loadingProgressCallback(lineIdx / lines.length, "Generating Voxel Map...");
                await pauseProcessing();
            }
        }

        this.loadingProgressCallback(lineIdx / lines.length, "Rendering Voxel...");


        let particleCount = 0;
        /********************************************************************************************** */
        /*                                 Build the 3d model                                           */
        /********************************************************************************************** */
        for (let vy = 0; vy < maxRenderedLayer; vy++) {
            let layerParticles = [];
            if (layers[vy] === undefined) continue;

            let minFilePosition = 999999999999;
            let maxFilePosition = -999999999999;
            for (let [x, zValues] of Object.entries(layers[vy])) {
                for (let [z, voxel] of Object.entries(zValues)) {

                    if (voxel.voxelEvents[0].filePosition < minFilePosition)
                        minFilePosition = voxel.voxelEvents[0].filePosition;

                    if (voxel.voxelEvents.slice()[0].filePosition > maxFilePosition)
                        maxFilePosition = voxel.voxelEvents[0].filePosition;

                    layerParticles.push({
                        x: x * this.voxelWidth,
                        y: vy * this.voxelHeight,
                        z: z * this.voxelWidth,
                        color: voxel.color.clone(),
                        voxelEvents: voxel.voxelEvents
                    })
                }
            }
            particleCount += layerParticles.length;
            layers[vy] = null;

            let sps = new SolidParticleSystem('gcodemodel' + meshIndex++, this.scene, {
                updatable: true,
                enableMultiMaterial: true,
                useVertexAlpha: false
            });

            //Build this layers particles
            let particleBuilder = (p, i, s) => {
                p.position.x = layerParticles[i].x;
                p.position.y = layerParticles[i].y;
                p.position.z = layerParticles[i].z;
                p.color = layerParticles[i].color;
                p.materialIndex = 0;
            };

            //We have to add props this way because the particle builder won't carry it over.
            sps.initParticles = () => {
                for (let p = 0; p < sps.nbParticles; p++) {
                    const particle = sps.particles[p];
                    particle.props = {
                        originalColor: layerParticles[p].color,
                        voxelEvents: layerParticles[p].voxelEvents,
                        lastDrawnCount: 0
                    };
                }
            };

            if (sps === null) {
                console.warn("Error init sps.");
                continue;
            }

            sps.addShape(box, layerParticles.length, {
                positionFunction: particleBuilder,
            });

            //SPS Init
            sps.initParticles();
            sps.buildMesh();
            sps.setMultiMaterial([this.solidMat, this.transparentMat]);
            sps.mesh.alphaIndex = 0;
            sps.mesh.isPickable = false
            sps.mesh.doNotSyncBoundingInfo = true
            let scrubbing = false
            let firstPass = true

            //Called when a voxel/particle is being se
            sps.updateParticle = function (particle) {
                if (particle.props === null) {
                    //Look into why props are coming across null
                    return;
                }

                particle.materialIndex = 1;
                var lastEvent = particle.props.voxelEvents.filter(ev => ev.filePosition < that.currentFilePosition).slice(-1)[0];
                if (lastEvent === null || lastEvent === undefined) return;
                if (lastEvent.add) {
                    particle.materialIndex = 0;
                    if (lastEvent.filePosition > lastPosition && lastEvent.filePosition <= that.currentFilePosition) {
                        particle.props.lastDrawnCount = 0;
                    }
                    if (scrubbing) {
                        particle.props.lastDrawnCount = VoxelRenderer.drawDelay;
                    }
                    if (particle.props.lastDrawnCount < VoxelRenderer.drawDelay) {
                        if (particle.color) {
                            particle.color = null;
                        }
                        particle.color = new Color4(0, 1, 0, 1);
                        particle.props.lastDrawnCount++;
                    }
                    else {
                        particle.color = particle.props.originalColor
                    }
                }
                else {
                    if (lastEvent.filePosition >= lastPosition && lastEvent.filePosition <= that.currentFilePosition) {
                        particle.props.lastDrawnCount = 0;
                    }
                    if (scrubbing) {
                        particle.props.lastDrawnCount = VoxelRenderer.drawDelay;
                    }
                    if (particle.props.lastDrawnCount < VoxelRenderer.drawDelay) {
                        particle.materialIndex = 0;
                        if (particle.color) {
                            particle.color = null;
                        }
                        particle.color = new Color4(1, 0, 0, 0.5);
                        particle.props.lastDrawnCount++;
                    }
                    else {
                        particle.materialIndex = 1;
                    }
                }
            };

            if (this.loadingProgressCallback) {
                this.loadingProgressCallback(vy / maxRenderedLayer, 'Rendering Voxels...');
            }

            let lastPosition = 0;

            let timeStamp = Date.now()
            //Start particle animation
            let beforeRenderFunc = function () {
                if (that.isLoading) return;
                if (Date.now() - timeStamp < 200) {
                    return;
                }
                timeStamp = Date.now();
                //Deal with time scrubbing
                if (Math.abs(lastPosition - that.currentFilePosition) > that.scrubDistance || firstPass) {
                    scrubbing = true;
                    sps.setParticles();
                    sps.computeSubMeshes();
                    firstPass = false;
                } else {
                    //Only update this SPS segment
                    if (that.currentFilePosition >= minFilePosition - 30000 && that.currentFilePosition <= maxFilePosition + 30000) {
                        scrubbing = false;
                        sps.setParticles();
                        sps.computeSubMeshes();
                    }
                }
                lastPosition = that.currentFilePosition;

            }
            //Register animation functions to main loop but keep track of them for deregistration later
            this.renderFuncs.push(beforeRenderFunc); //track our registered function
            this.scene.registerBeforeRender(beforeRenderFunc); //register render function

            if (new Date() - lastUpdate > 1000) {
                lastUpdate = new Date()
                await pauseProcessing();
            }
        }

        console.log(particleCount);
        layers = null;
        this.loadingProgressCallback(1);
        this.scene.clearCachedVertexData(); //Cleanup cache data.
        this.isLoading = false;

    }

}
