<template>
	<div class="bgcolor">
		<v-btn @click="save()" block>Save Tools</v-btn>
		<v-card :key="idx" class="mb-2" v-for="(tool, idx) in tools">
			<v-card-title>{{tool.name}}</v-card-title>
			<v-card-text>
				<v-row>
					<v-col cols="12">
						<div class="mb-2">Filamnt Color</div>
						<color-picker :editcolor.sync="tool.colorString"></color-picker>
					</v-col>
					<v-col cols="12">
						Diameter
						<v-text-field v-model="tool.diameter"></v-text-field>
					</v-col>
					<v-col cols="12">
						Tool Type
						<v-select :items="toolTypes" v-model="tool.toolType">
							
						</v-select>
					</v-col>
				</v-row>
			</v-card-text>
		</v-card>
	</div>
</template>

<script>
import {GetTool, SaveTool} from './viewer/tool.js';

export default {
	data() {
		return {
			toolCountValue: 1,
			tools: new Array(),
			toolTypes: ["Extruder", "Endmill"]
		};
	},
	mounted() {
		this.tools = GetTool();
	},
	methods: {
		save() {
			SaveTool();
		},
	},
	computed: {
		toolCount: {
			get: function () {
				return this.toolCountValue;
			},
			set: function (value) {
				console.log(value);
				if (value > 10 || value < 1) {
					return;
				}
				this.toolCountValue = value;
				console.log(`TCV ${this.toolCountValue}`);
			},
		},
	},
};
</script>

<style>
.bgcolor {
	background-color: black;
}

</style>